.Instagram {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.Instagram p {
    font-weight: 300;
    margin: 0.5rem 0;
    font-size: 14px;
}

.Instagram hr {
    width: 75px;
    border: 1.5px solid black;
}

.instagram-wrapper {
    display: flex;
    max-width: 1267px;
    overflow-x: auto;
    padding-top: 1rem;
}

.instagram-wrapper img {
    width: 246.738px;
}

.Instagram button {
    margin: 1.5rem auto;
    width: fit-content;
    padding: 0.75rem 1rem;
    border: 0;
    background-color: #4C2160;
    color: white;
    font-size: 14px;
}